import Vue from 'vue';
import {eventEmitter} from '@/main';

export default Vue.extend({
    data: () => ({
        avatarUrl: '',
    }),
    computed: {
        avatarPath() {
            let url: string|null = (this.$store.getters['user/userAvatar'])();
            if (! url && this.avatarUrl.length > 0) {
                url = this.avatarUrl;
            }
            //console.log(url);
            return url;
        },
    },
    created(): void {
        eventEmitter.$on(
            'avatarChanged',
            ($event: {url: string}) => {
                this.avatarUrl = $event.url;
            },
        );
    },
});
