
































import Vue from 'vue';
import Common from '@/core/mixins/Common';
import CircularSpinner from '@/core/components/Utils/CircularSpinner.vue';
import {Lead} from '@/core/store/lead/types';
import LeadsList from '@/core/components/Lead/LeadsList.vue';
import {User} from '@/core/store/user/types';

export default Vue.extend({
    mixins: [Common],
    components: {LeadsList, CircularSpinner},
    computed: {
        leads(): Array<Lead>|undefined {
            return this.$store.getters['lead/activeLeads'];
        },
    },
    data() {
        return {isActive: false, alertThank: false, name: '', phone: ''};
    },
    mounted(): void {
        if (sessionStorage.getItem('showThankYouPage')) {
            const user: User = this.$store.getters['user/userObj'];
            if (typeof user !== 'undefined' && user.state === 2) {
                this.name = user.firstName !== undefined ? user.firstName : '';
                this.phone = user.phone;
                this.alertThank = true;
            }
        }
    },
    watch: {
        alertThank(val) {
            if (! val) {
                sessionStorage.removeItem('showThankYouPage');
            }
        },
    },
});
