import {ActionTree} from 'vuex';
import axios from '@/core/plugins/axios';
import {AxiosResponse} from 'axios';
import {ProfileState, User} from './types';
import {RootState} from '../types';

export const actions: ActionTree<ProfileState, RootState> = {
    async registerPhone({commit}, payload: User) {
        commit('clearError', null, {root: true});
        const url = 'auth/client/registerPhoneStepOne';

        const user = payload;

        try {
            await axios.post(url, payload).then((response: AxiosResponse<any>) => {
                const data = response && response.data;
                console.log(data);
                if (data.status === 'success') {
                    user.state = data.state;
                    commit('profileLoaded', user);

                    /**
                     * если отключен сервис смс (то придет code в ответе сервера)
                     * и это demo сервер, то выводим alert с кодом активации
                     */
                    if (process.env.VUE_APP_API_URL.indexOf('demo') >= 0 && typeof data.code !== 'undefined') {
                        //eslint-disable-next-line no-alert
                        alert('Activation code:' + data.code);
                    }
                }
            }, (error) => {
                console.log(error.response.data.errors);
                commit('setError', error.response.data.errors, {root: true});
                commit('profileError');
            });
        } catch (e) {
            commit('setError', e, {root: true});
        }
    },
    async activateUserPhone({commit, getters}, payload: {code: number; phone: string}) {
        commit('clearError', null, {root: true});

        const url = 'auth/client/activationPhone';
        try {
            await axios.post(url, payload).then((response: AxiosResponse<any>) => {
                const data = response && response.data;
                console.log(data);
                const user = getters.userObj;
                if (data.status === 'success') {
                    if (typeof data.token !== 'undefined') {
                        localStorage.setItem('token', data.token);
                        user.email = data.email;
                        user.firstName = data.first_name;
                        user.lastName = data.last_name;
                        user.state = data.state;
                        user.id = data.id;
                        if (data.avatarPath !== undefined && data.avatarPath.length > 0) {
                            user.avatarPath = data.avatarPath;
                        }
                        commit('profileLoaded', user);
                    } else {
                        commit('changeUserState', 1);
                    }
                } else if (data.status === 'fail') {
                    commit('setError', {code: [data.message]}, {root: true});
                }
            }, (error) => {
                console.log(error);
                console.log(error.response.data.errors);
                commit('setError', error.response.data.errors, {root: true});
            });
        } catch (error) {
            commit('setError', error, {root: true});
        }
    },
    async updateUserProfile({commit}, payload: User) {
        commit('clearError', null, {root: true});
        console.log(payload);
        const dataRequest: any = {
            id: payload.id,
            firstName: payload.firstName,
            lastName: payload.lastName,
        };
        if (payload.email !== undefined && payload.email?.length > 0) {
            dataRequest.email = payload.email;
        }

        let data = null;

        const url = 'client/profileSave';
        try {
            await axios.post(url, dataRequest).then((response: AxiosResponse<any>) => {
                data = response && response.data;
                console.log(data);
                if (data.status === 'success') {
                    commit('profileLoaded', payload);
                }
            }, (error) => {
                console.log(error.response.data.errors);
                commit('setError', error.response.data.errors, {root: true});
            });
        } catch (error) {
            commit('setError', error, {root: true});
            throw error;
        }
        return data;
    },
    async updateUserAvatar({commit}, payload: {id: number; avatar: any}) {
        commit('clearError', null, {root: true});
        let resp = null;

        const url = 'client/avatarSave';
        //console.log(payload);
        const formData = new FormData();
        formData.append('id', String(payload.id));
        formData.append('file', payload.avatar);
        try {
            await axios.post(url, formData).then((response: AxiosResponse<any>) => {
                resp = response && response.data;
                console.log(resp);
                if (resp.status === 'success' && resp.avatar !== undefined) {
                    commit('changeUserAvatar', resp.avatar);
                }
            }, (error) => {
                console.log(error.response.data.errors);
                commit('setError', error.response.data.errors, {root: true});
            });
        } catch (error) {
            commit('setError', error, {root: true});
            throw error;
        }
        return resp;
    },
    async refreshUser({commit}) {
        commit('clearError', null, {root: true});
        const url = 'auth/client/refreshClient';

        try {
            await axios.post(url, {}).then((response: AxiosResponse<any>) => {
                const data = response && response.data;
                console.log(data);
                if (data.status === 'success') {
                    commit('profileLoaded', data.user);
                } else {
                    localStorage.removeItem('token');
                }
            }, (error) => {
                console.log(error.response.data.errors);
                commit('setError', error.response.data.errors, {root: true});
                commit('profileError');
                localStorage.removeItem('token');
            });
        } catch (error) {
            commit('setError', error, {root: true});
            localStorage.removeItem('token');
        }
    },
    logoutUser({commit}) {
        localStorage.removeItem('token');
        commit('profileLoaded', undefined);
    },
};
