






































































import Vue from 'vue';
import Common from '@/core/mixins/Common';
import {User} from '@/core/store/user/types';
import ValidationRules from '@/core/mixins/ValidationRules';
import Avatar from '@/components/User/Avatar.vue';
import CircularSpinner from '@/core/components/Utils/CircularSpinner.vue';
import Notifications from '@/core/components/Utils/Notifications.vue';
import i18n from '@/core/plugins/i18n';
// eslint-disable-next-line import/no-cycle
import AvatarMixin from '@/core/mixins/Avatar';

export default Vue.extend({
    mixins: [ValidationRules, Common, AvatarMixin],
    components: {Avatar, CircularSpinner, Notifications},
    data() {
        return {
            firstName: '',
            lastName: '',
            loading2: false,
            alertNotification: false,
            notificationText: '',
            notificationType: 'success',
        };
    },
    methods: {
        saveProfileData() {
            this.loading2 = true;
            const user: User = this.$store.getters['user/userObj'];
            user.email = this.$data.email;
            user.firstName = this.firstName;
            user.lastName = this.lastName;
            this.$store.dispatch('user/updateUserProfile', user).then((response) => {
                //console.log(response);
                this.loading2 = false;
                if (response.status === 'success') {
                    this.notificationText = String(i18n.t('profile.alert_success'));
                    this.alertNotification = true;

                    this.$vuetify.goTo('#alertMessage', {
                        duration: 300,
                        offset: 0,
                        easing: 'easeInOutCubic',
                    });
                }
            })
                .catch((err) => console.log(err));
        },
    },
    mounted() {
        const user: User = this.$store.getters['user/userObj'];
        if (typeof user !== 'undefined' && user.state === 2) {
            this.firstName = user.firstName !== undefined ? user.firstName : '';
            this.lastName = user.lastName !== undefined ? user.lastName : '';
            this.$data.email = user.email !== undefined ? user.email : '';
        }
    },
});
