





































































import Vue from 'vue';
import Common from '@/core/mixins/Common';
import PhoneRegistration from '@/core/components/Auth/PhoneRegistration.vue';
import {SphereArr} from '@/core/store/sphere/types';

export default Vue.extend({
    components: {PhoneRegistration},
    props: {
        isFixed: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            dialogActivator: false,
            needRegister: false,
            search: '',
            isHome: true,
        };
    },
    mixins: [Common],
    methods: {
        redirectToSphereForm(sphereId: number) {
            this.$data.needRegister = true;
            this.$data.dialogActivator = false;
            localStorage.setItem('sphereRoute', '/create/lead/' + sphereId);
            this.$router.push('/create/lead/' + sphereId);
        },
    },
    computed: {
        spheres() {
            return this.$store.getters['sphere/getUniversalShownSpheres'];
        },
        filteredSpheres() {
            let fSpheres = this.$store.getters['sphere/getUniversalShownSpheres'];
            const searchStr = this.$data.search.toLowerCase();
            //console.log(searchStr);
            if (searchStr.length > 0) {
                fSpheres = fSpheres.filter(
                    (sphere: SphereArr) => sphere.sphere.name.toLowerCase().includes(searchStr, 0),
                );
            }
            return fSpheres;
        },
    },
    watch: {
        $route(to) {
            //console.log(to);
            this.$data.isHome = to.path === '/';
        },
    },
});
