




















































































































































































































import Vue from 'vue';
import PhoneRegistration from '@/core/components/Auth/PhoneRegistration.vue';
import SpheresListModal from '@/core/components/Lead/SpheresListModal.vue';
import LanguageSwitcher from '@/core/components/Utils/LanguageSwitcher.vue';
import Common from '@/core/mixins/Common';
import i18n from '@/core/plugins/i18n';
// eslint-disable-next-line import/no-cycle
import Avatar from '@/core/mixins/Avatar';

export default Vue.extend({
    mixins: [Common, Avatar],
    components: {
        appPhoneRegistration: PhoneRegistration,
        LanguageSwitcher,
        SpheresListModal,
    },
    data: () => ({
        drawer: false,
        logoUrl: 'data:image/svg+xml;base64,',
        messages: 5,
        needRegister: false,
        showLogin: false,
    }),
    computed: {
        links() {
            return [
                {title: i18n.t('profile.title'), icon: 'mdi-account', url: '/profile'},
            ];
        },
        fullName() {
            return this.$store.getters['user/fullName'];
        },
        checkRightPosition() {
            return i18n.locale === 'en';
        },
        tabs() {
            return [
                //{title: i18n.t('tabs.main'), url: '/'},
                {title: i18n.t('tabs.requests'), url: '/requests'},
                //{title: i18n.t('tabs.scheduler'), url: '/scheduler'},
                {title: i18n.t('tabs.history'), url: '/history'},
            ];
        },
        noToken() {
            return localStorage.getItem('token') === null;
        },
        getStyleForContactBtn() {
            if (i18n.locale === 'en') {
                return {zIndex: '9', top: '80vh', right: '100px'};
            }
            return {zIndex: '9', top: '80vh', left: '100px'};
        },
    },
    methods: {
        onLogout() {
            this.$store.dispatch('user/logoutUser');
            this.$store.dispatch('lead/clearLeads');
            this.needRegister = false;
            this.showLogin = true;
            if (this.$router.currentRoute.path !== '/') {
                this.$router.push('/');
            }
        },
    },
});
