






































































































import Vue from 'vue';
// eslint-disable-next-line import/no-cycle
import {eventEmitter} from '@/main';

export default Vue.extend({
    props: ['src'],
    data() {
        return {
            avatarDialog: false,
            avatar: null,
            cropperShow: false,
            cropperResult: null,
            loadingAvatar: false,
            allFilesSize: 0,
        };
    },
    methods: {
        uploadAvatar() {
            this.loadingAvatar = true;

            try {
                const options = {
                    type: 'blob',
                };

                const userId: number|null = this.$store.getters['user/userId'];

                const refs: any = this.$refs.croppieRef;

                if (refs && userId) {
                    refs.result(options, async (output: any) => {
                        this.$store.dispatch('user/updateUserAvatar', {id: userId, avatar: output}).then((resp) => {
                            this.loadingAvatar = false;
                            this.avatarDialog = false;
                            //console.log(resp);
                            if (resp.status === 'success' && resp.avatar !== undefined) {
                                eventEmitter.$emit('avatarChanged', {
                                    url: resp.avatar,
                                });
                            }
                        })
                            .catch((err) => console.log(err));
                    });
                }
            } catch (e) {
                console.log(e);

                this.loadingAvatar = false;
            }
        },
        cropperInit() {
            if (this.cropperShow) {
                return;
            }

            this.cropperShow = true;

            const reader = new FileReader();

            reader.onload = () => {
                const url = reader.result;

                const ref: any = this.$refs.croppieRef;
                if (ref) {
                    ref.bind({
                        url,
                    });
                }
            };

            if (this.avatar !== null) {
                // eslint-disable-next-line
                // @ts-ignore
                reader.readAsDataURL(this.avatar);
            }
        },
        closeModal() {
            this.avatarDialog = false;
            this.avatar = null;
            this.cropperResult = null;
            this.cropperShow = false;
        },
    },
    watch: {
        avatar() {
            if (this.avatar) {
                this.cropperInit();
            }
        },
    },
});
