import {User} from '@/core/store/user/types';
import store from '../store';

export default function (to: any, from: any, next: any) {
    let token: string|null = localStorage.getItem('token');
    const user: User = store.getters['user/userObj'];
    let newPath: {name: string; params: {id: string; leadUuid: string}}|null = null;

    //проверка query параметров
    // eslint-disable-next-line no-restricted-globals
    const searchStr = location.search;
    if (searchStr !== '') {
        const params: any = searchStr
            .replace('?', '')
            .split('&')
            .reduce(
                (p, e) => {
                    const a = e.split('=');
                    // eslint-disable-next-line no-param-reassign,@typescript-eslint/ban-ts-ignore
                    // @ts-ignore
                    // eslint-disable-next-line no-param-reassign
                    p[decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                    return p;
                },
                {},
            );

        if (typeof params['token'] !== 'undefined' && params['token'].length > 0) {
            token = params['token'];
            if (token != null) {
                localStorage.setItem('token', token);
                sessionStorage.setItem('showThankYouPage', '1');
                if (params['uuid'] !== undefined) {
                    //localStorage.setItem('uuid', params['uuid']);
                    newPath = {name: 'leadEdit', params: {id: params['sphere_id'], leadUuid: params['uuid']}};
                }
            }
        }
    }

    const spheres = store.getters['sphere/getUniversalSpheres'];

    if (token && typeof token !== 'undefined' && typeof user === 'undefined') {
        //если токен есть, а юзера нет, находим юзера
        store.dispatch('user/refreshUser').then(() => {
            if (newPath) {
                if (spheres.length === 0) {
                    store.dispatch('sphere/fetchUniversalSpheres').then(() => {
                        next(newPath);
                    });
                } else {
                    next(newPath);
                }
            } else if (to.path === '/') {
                next('requests');
            } else {
                next();
            }
        });
    } else {
        next();
    }

    if (spheres.length === 0) {
        store.dispatch('sphere/fetchUniversalSpheres');
    }
}
