import {RouteConfig} from 'vue-router';
import History from '@/components/Lead/History.vue';
import HomeVideo from '@/components/HomeVideo.vue';
import SphereLeadForm from '@/core/components/Lead/SphereLeadForm.vue';
import Requests from '@/components/Lead/Requests.vue';
import LeadDetails from '@/components/Lead/LeadDetails.vue';
import Profile from '@/core/components/User/Profile.vue';
import CheckSphereAttributes from './check_sphere_attributes';
import CheckLeads from './check_leads';
import CheckLeadDetails from './check_lead_details';

export const routes: Array<RouteConfig> = [
    {
        path: '/',
        name: 'home',
        component: HomeVideo,
    },
    {
        path: '/profile',
        name: 'profile',
        component: Profile,
    },
    {
        path: '/requests',
        name: 'requests',
        component: Requests,
        beforeEnter: CheckLeads,
    },
    {
        path: '/history',
        name: 'history',
        component: History,
        beforeEnter: CheckLeads,
    },
    {
        path: '/create/lead/:id/:leadId?',
        props: true,
        name: 'leadCreate',
        component: SphereLeadForm,
        beforeEnter: CheckSphereAttributes,
    },
    {
        path: '/show/lead/:id/:sphereId',
        props: true,
        name: 'showLead',
        component: LeadDetails,
        beforeEnter: CheckLeadDetails,
    },
    {
        path: '/edit/lead/:id/:leadUuid',
        props: true,
        name: 'leadEdit',
        component: SphereLeadForm,
        beforeEnter: CheckSphereAttributes,
    },
];
